@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

*{
    font-family:"Poppins", serif ;
}

body{
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    background-image: url('./assets/bg4.webp');
    background-position: center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container{
    width: 320px;
    padding: 20px;
    border-radius: 8px;
    background:rgba(255,255,255,0.2);
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.input-container{
    display: flex;
    width: 100%;
    align-items: center;
    border: 2px solid #ffffff94;
    border-radius: 10px;
    overflow:hidden;
    background:rgba(255,255,255,0.01);
}

.cityInput{
    flex: 1;
    height: 23px;
    border: none;
    outline: none;
    font-size: 14px;
    text-indent: 5px;
    background:rgba(255,255,255,0.01); 
}

.search-icon{
    padding: 10px;
    height: 23px;
    width: 15px;
    cursor: pointer;
}
.search-icon img{
    width: 15px;
    margin-top: 5px;
}

.image{
    text-align: center;
    margin: 12px 0;
}

.image img{
    width: 120px;
    height: 120px;
}

.temp, .location, .country {
    font-size: 14px;
    color: #fff;
    text-transform: uppercase;
    text-align: center;
}

.temp{
    font-weight: 500;
}

.location{
    color:#ff009dcb;
    font-weight: 600;
    font-size: 20px;
}

.country{
    font-size: 15px;
    color: #fff;
    font-weight: 500;
}

.cord{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 7px;
    text-align: center;
    font-size: 14px;
    color: #ffffffe7;
}

.cord div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cord div span{
    font-size: 12px;
}


.humidity-precent,
.wind-precent,
.cord div span :nth-child(2){
    font-size: 14px;
    font-weight: bold;
    color: #ffffffe7;
    padding-top: 5px;
}

.data-container{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.element{
    text-align: center;
}

.element .icon{
    width: 23px;
    height: 23px;
}

.text{
    font-size: 12px;
    color: #fff;
}

.copyright{
    text-align: center;
    font-size: 12px;
    color:#7a7064;
}

.copyright span{
    color: #ffffffb2;
}
